import * as React from "react"
import { Link, graphql, PageProps } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { FC } from "react"
import { Post } from "../models/post"
import { SiteMetadata } from "../models/siteMetadata"
import { PostFrontmatter } from "../models/postFrontmatter"

type DataProps = {
  markdownRemark: Post
  previous: Post
  next: Post
  site: {
    siteMetadata: SiteMetadata
  }
}

const BlogPostTemplate : FC<PageProps<DataProps>> = ({
  data: { previous, next, site, markdownRemark: post },
  location,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`

  return (
    <Layout location={location} title={siteTitle}>
      <article
        className="blog-post"
        itemScope
        itemType="http://schema.org/Article"
      >
        <header>
        {
          post.frontmatter.igId
            ? <a className="ig-post-link"
              href={`https://www.instagram.com/p/${post.frontmatter.igId}`}>
              Mira les fotos a Instagram!
            </a>
            : null
        }
          <h1 itemProp="headline">{post.frontmatter.title}</h1>
          <p>{post.frontmatter.date}</p>
        </header>
        <section
          dangerouslySetInnerHTML={{ __html: preparePostContent(post.html, post.frontmatter) }}
          itemProp="articleBody"
        />
        <hr />
        <footer>
          <Bio />
        </footer>
      </article>
      <nav className="blog-post-nav">
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                ← {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  )
}

export const Head = ({ data: { markdownRemark: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

function preparePostContent(postContent: string, postFrontmatter: PostFrontmatter): string {
  let output = convertIgMentionsInLinks(postContent)

  output = removeHastagsBlock(output)

  if (postFrontmatter.location) {
    output += createMap(postFrontmatter.location)
  }

  return output
}

function createMap(location: string): string {
  return `<iframe
      src="https://www.google.com/maps/embed?pb=${location}"
      style="border: 0; width: 100%"
      width="600"
      height="450"
      allowfullscreen=""
      loading="lazy"
      referrerpolicy="no-referrer-when-downgrade">
    </iframe>`
 }

function removeHastagsBlock(postContent: string): string {
  const hashtagsParagraph = /<p>(#[\wàèéíòóú]+\s?){5,}<\/p>/i

  return postContent.replace(hashtagsParagraph, "")
}

function convertIgMentionsInLinks(postContent: string): string {
  return postContent.replaceAll(
    /@([\w\.]*)/g,
    `<a href="https://www.instagram.com/$1">$&</a>`
  )
}

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "DD MMMM YYYY", locale: "ca")
        description,
        igId,
        location
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
